
.pictureWrapper {
    background-color: var(--darkGrayColor);
    background-size: contain;
    cursor: pointer;
    display: inline-block;
    height: 26px;
    width: 26px;
}

.paletteWrapper {
    position: absolute;
    z-index: 1000;
    content: "";
    width: 125px;
    height:  210px;
    background-color:  var(--whiteColor);
    box-shadow: 0 0 5px 5px var(--lightGrayColor);
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    flex-wrap: wrap;
    padding: 7px;
}

.circleButton {
    width: 15px;
    height:  15px;
    margin: 4px 2px;
    border-radius: 10px;
    border: 1px solid var(--lightGrayColor);

}

.circleButton:hover {
    box-shadow: 0 0 2px 2px var(--lightYellowColor);
    scale: 1.1;
}

